import * as React from 'react';
import Section from '../Section';
import BackgroundSlider from '../BackgroundSlider';
import { Image } from '@chakra-ui/core';
import { SliderItem } from '../BackgroundSlider/BackgroundSlider';

export default function Partners({ firstRow, secondRow, ...props }) {
  return (
    <Section {...props}>
      <BackgroundSlider numberOfElements={firstRow.length} duration={40}>
        {firstRow.map((img) => (
          <SliderItem key={img}>
            <Image src={`/static/images/partners/${img}`} alt={img.replace('.png', '')} />
          </SliderItem>
        ))}
      </BackgroundSlider>
      <BackgroundSlider numberOfElements={secondRow.length} duration={50}>
        {secondRow.map((img) => (
          <SliderItem key={img}>
            <Image src={`/static/images/partners/${img}`} alt={img.replace('.png', '')} />
          </SliderItem>
        ))}
      </BackgroundSlider>
    </Section>
  );
}
